import React, { useContext, useEffect, useState } from "react";
import AuthContext from "../context/AuthContext";
import MakeBtn from "../components/MakeBtn";
import useBtn from "../hooks/btn.hook";
import { sha256 } from "js-sha256";
import { IDefaulAns } from "../models/sspvo";
import { f } from "../ts/utils";
import { DS } from "../models/common";

interface Region {
    Id: number;
    Code?: any;
    Name: string;
    Created?: any;
    actual: boolean;
}

interface Role {
    id: number;
    name: string;
    code: string;
    description?: string;
    created: Date;
    actual: boolean;
}

interface UserInfo {
    id: number;
    login: string;
    pass_period: Date;
    patronymic: string;
    surname: string;
    name: string;
    id_region: number;
    region: Region;
    role: Role;
    registration_date: DS;
    post: string;
    work?: string;
    adress?: string;
    phone?: string;
    email: string;
    id_Author?: number;
    actual: boolean;
    update_at?: DS;
    snils?: string;
}

interface OrganizationsList {
    full_title: string;
    id: number;
    id_eiis?: number;
    is_oovo: boolean;
    kpp: string;
    ogrn: string;
    short_title: string;
    timezone: number;
}

interface LoginAns {
    user_info: UserInfo;
    organizations_list: OrganizationsList[];
    done: boolean;
    message: string;
}

const AuthPage: React.FC = () => {

    const [form, setForm] = useState({ login: "", password: "" });
    const [error, setError] = useState(false);
    const auth = useContext(AuthContext);
    const loginBtn = useBtn("logIn", "Войти", false);

    useEffect(() => { document.title = "Авторизация"; }, []);

    const loginHandler = async (e: React.MouseEvent) => {
        e.preventDefault();
        loginBtn.disable();
        const ans = await f.post<LoginAns>("/api/login", {
            login: form.login,
            password: sha256(form.login.toLocaleUpperCase() + form.password)
        });

        if (ans.done) {
            const { name, surname, patronymic } = ans.user_info;
            const orgAns = await f.post<IDefaulAns>("/api/user/current-org", { id: ans.organizations_list[0].id });
            if (orgAns.done) {
                auth.login(`${name} ${patronymic} ${surname}`, ans.organizations_list[0].short_title);
            }
        } else {
            setError(true);
            loginBtn.enable();
        }
    };

    const changeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
        e.preventDefault();
        setError(false);
        setForm({ ...form, [e.target.name]: e.target.value });
    };

    return (
        <div className="auth">
            <h2 style={{ color: "white" }}>Авторизация</h2>
            <form className="auth">
                <input
                    placeholder="Логин"
                    id="login"
                    type="text"
                    name="login"
                    autoComplete="on"
                    // value={form.login}
                    onChange={changeHandler}
                />
                <input style={{ marginTop: "4px" }}
                    placeholder="Пароль"
                    id="password"
                    type="password"
                    name="password"
                    autoComplete="on"
                    // value={form.password}
                    onChange={changeHandler}
                />
                {error && <p style={{ color: "deeppink" }}>Неверные данные!</p>}
                <MakeBtn p={loginBtn.p} func={loginHandler} column={true} />
            </form>
        </div>
    );
};

export default AuthPage;